.our-team {

    background: url('./components/MainPage/img/mainPageBac.jpg') no-repeat;
    padding-bottom: 67px;

    &__body {
        
    }

    &__container {
        max-width: 1300px;
        overflow: hidden;
        margin-left: 141px;
        margin-right: 10px;
    }

    &__h2 {
        @include heading('Arkhip', 64px, 77px, #3446EE);
        padding: 65px 0 0 0;
    }

    &__text-box {
        display: block;
        max-width: 820px;
        @include heading('Montserrat', 24px, 140%, #000);
        font-style: normal;
        font-weight: 400;
        margin: 0 auto;
        text-align: center;
        padding: 100px 0 5px 0;
    }

    &__item-wrapper {
        position: relative;
        transition: all .5s;
        overflow: hidden;
        height: 634px;

        transition: height .5s;
        
    }

    &__prev-info {
        position: absolute;
        z-index: 99999;
        padding: 58px 0 0 10px;
        display: none;
    }
    &__prev-name {
        @include heading('Montserrat', 24px, 29px, $color-white);
        font-weight: 700;
        padding-bottom: 15px;
    }
    &__prev-follows {
        @include heading('Montserrat', 20px, 24px, $color-white);
        font-weight: 400;
    }


    &__img-wrapper {
        position: absolute;
        width: 413px;
        height: 634px;
        margin-top: 20px;

        transition: all .5s;
    }

    &__img {
        transition: all .5s;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 0 0;
    }

    &__item-info {
        padding-left: 445px;
    }

    &__btn {
        width: fit-content;
        width: 50px;
        height: 50px;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        position: absolute;
        z-index: 9999;
        right: 30px;
        top: 57px;
        transition: all .5s;
    }

    &__btn-close {
        z-index: 999;
    }

    &__blogger-name {
        @include heading('Arkhip', 48px, 58px, #3446EE);
        text-align: center;
        padding: 60px 0;
    }

    &__blogger-info {
        max-width: 526px;
        @include heading('Montserrat', 24px, 140%, #000);
        margin: 0 auto;
        text-align: center;
        padding-bottom: 10px;
    }

    &__amount-followers {
        @include heading('Montserrat', 24px, 140%, #000);
        text-align: center;
        padding-bottom: 5px;
    }

    &__amount-social {
        @include heading('Montserrat', 24px, 140%, #000);
        text-align: center;

        & span {
            font-weight: 600;
        }
    }

    &__social-labels {
        display: flex;
        gap: 30px;
        justify-content: center;
    }

    &__button {
        margin: 47px 0 36px 0;
    }


    &__item-wrapper-animation {

        width: 1300px;
        height: 143px;
        transition: all .5s;

        &__wrapper-item {
            height: 143px;
            transition: height .5s;
        }

        & img {
            transition: all .5s;
            object-fit: cover;
            object-position: 0 -597px;
            transition: all .5s;
        }

        &.position-alice {

            & img {
                object-position: 0 -597px;    
            }
        }
        &.position-sofia {

            & img {
                object-position: 0 -1055px;    
            }
        }
        &.position-bogdan {

            & img {
                object-position: 0 -692px;    
            }
        }
        &.position-yaroslav {

            & img {
                object-position: 0 -607px;    
            }
        }

        &__item-info {
            z-index: -1;
            display: none;
        }

        &__prev-info {
            display: block;
        }

        &__close-btn {
            transform: rotate(45deg);
            transition: all .5s;

            & path {
                fill: $color-hard-red;
            }
        }

        &__position-alice {
            object-position: 0 -597px;
            transition: all .5s;
        } 
    
        &__position-sofia {
            object-position: 0 -1055px;
            transition: all .5s;
        }
    
        &__position-bogdan {
            object-position: 0 -692px;
            transition: all .5s;
        }
    
        &__position-yaroslav {
            object-position: 0 -607px;
            transition: all .5s;
        }

    }

    &__btn-detail {
        margin: 50px 0 0 auto;
        color: #EE3434;

        & .blue-button__subdiv:nth-child(1) {
            border-left: 3px solid #EE3434;
            border-top: 3px solid #EE3434;
        }

        & .blue-button__subdiv:nth-child(2) {
            border-right: 3px solid #EE3434;
            border-top: 3px solid #EE3434;
        }
        & .blue-button__subdiv:nth-child(3) {
            border-left: 3px solid #EE3434;
            border-bottom: 3px solid #EE3434;
        }

        & .blue-button__subdiv:nth-child(4) {
            border-right: 3px solid #EE3434;
            border-bottom: 3px solid #EE3434;
        }

        &:hover {
    
            .blue-button__subdiv {
            
        
                &:nth-child(3) {
                    border-right: 3px solid #EE3434;
                    border-bottom: 3px solid #EE3434;
                }
                
                &:nth-child(2) {
                    border-left: 3px solid #EE3434;
                    border-top: 3px solid #EE3434;
                }
        
            }
        }
    }

    &__btn-items {
        margin: auto auto 0 0;
    }
}

.svg {
    fill: $color-hard-red;
    margin: 15px;
    cursor: pointer;
    position: relative;
    width: fit-content;

    :nth-child(n){
        transition: all .6s ease 0s;
    }

    &__top-right-line {
        position: absolute;
        top: -5px;
        left: 5px;
        width: 27px;
        height: 3px;
        background-color: $color-hard-red;
    }

    &__right-top-line {
        position: absolute;
        top: 7px;
        left: 20px;
        width: 27px;
        height: 3px;
        background-color: $color-hard-red;
        transform: rotate(90deg);
    }

    &__bottom-left-line {
        position: absolute;
        bottom: -3px;
        right: 6px;
        width: 27px;
        height: 3px;
        background-color: $color-hard-red;
    }

    &__left-bottom-line {
        position: absolute;
        bottom: 9px;
        right: 20px;
        width: 27px;
        height: 3px;
        background-color: $color-hard-red;
        transform: rotate(90deg);
    }

    &:hover{

        svg{
            fill: $color-light-blue;

            & path {
                fill: $color-light-blue;
            }
        }

        .svg__top-right-line,
        .svg__right-top-line,
        .svg__bottom-left-line,
        .svg__left-bottom-line {
            background-color: blue;
        }

        .svg__top-right-line{
            top: 32px;
        }

        .svg__right-top-line{
            transform: rotate(270deg);
            top: 20px;
        }

        .svg__bottom-left-line{
            bottom: 34px;
        }

        .svg__left-bottom-line{
            bottom: 22px;
        }
    }
}

@media (max-width: 1212px) {
    
    .our-team {

		&__blogger-name {
            @include heading('Arkhip', 35px, 58px, #3446EE);
            padding: 93px 0;
		}

		&__blogger-info {
            @include heading('Montserrat', 18px, 140%, #000);
            padding-bottom: 30px;
		}

		&__amount-followers {
            @include heading('Montserrat', 18px, 140%, #000);
		}

		&__amount-social {
            @include heading('Montserrat', 18px, 140%, #000);
		}
    }

}

@media (max-width: 972px) {
    
    .our-team {

		&__blogger-name {
            @include heading('Arkhip', 35px, 58px, #3446EE);
            padding: 93px 0 0 0;
		}

		&__blogger-info {
            @include heading('Montserrat', 17px, 140%, #000);
            padding-bottom: 0;
		}

		&__amount-followers {
            @include heading('Montserrat', 18px, 140%, #000);
		}

		&__amount-social {
            @include heading('Montserrat', 18px, 140%, #000);
		}

    }

}